/* ===Project Page=== */


/* banner section  */
.project-section{background-image: url(data:image/jpeg;base64,);}


/* corona section  */
.corona-card{background: #faf2e5; border-radius: 13px;padding: 50px 0;border: 1px solid #DCCBAE;
    margin-bottom: 2rem; display: flex;justify-content: center;flex-direction: column;
}
.corona-card .corona-result,.corona-card .corona-heading{ padding: 20px 0; width: 80%; margin:0 auto; text-align: center; font-weight: 700;}
.corona-card-india{background-image: linear-gradient(#FF9933, #FFFFFF, #138808)}
.corona-card .corona-heading{color: #6B4700; border-bottom: 1px solid #91889D;}
.corona-card .corona-result{font-size: 24px!important; color: #222}
.dot {animation: blink 2s infinite; background: #d63384; width: 10px; height: 10px;display: inline-block; border-radius: 50%;}  
@keyframes blink {
    0%, 100% { opacity: 0; }
    50% { opacity: 1; }
}
  

/* ===Media Query==== */

@media only screen and (max-width: 768px){	

.corona-card{margin-bottom: 1rem;}
.corona-card .corona-result,.corona-card .corona-heading{width: 90%; margin:0 auto;}

}


