/* ----css------ */


/* --------------------------------------------------------------------------
0. Font Family
------------------------------------------------------------------------- */
.fontFamily-BebasNeue{font-family: 'Bebas Neue', cursive;}


/* --------------------------------------------------------------------------
1. Basic Information
------------------------------------------------------------------------- */

/* 
Website Name: Devro
Text Domain: https://www.devro.com/
Description: Personal website
Author: Rohit 
*/

/* --------------------------------------------------------------------------
2. Element Base
------------------------------------------------------------------------- */

body { font-size: 18px;color: #7e7e7e !important; line-height: 1.7em; font-weight: 400; font-family: 'Lato', sans-serif;}
h1, .heading-size-1 { font-size: 40px; line-height: 50px !important;}
h2, .heading-size-2 { font-size: 30px; line-height: 40px !important;}
h3, .heading-size-3 { font-size: 28px; line-height: 38px !important;}
h4, .heading-size-4 { font-size: 25px; line-height: 35px !important;}
h5, .heading-size-5 { font-size: 22px; line-height: 32px !important;}
h6, .heading-size-6 { font-size: 19px; line-height: 29px !important;}
h1, .heading-size-1,h2, .heading-size-2,h3, .heading-size-3,
h4, .heading-size-4,h5, .heading-size-5,h6, .heading-size-6{letter-spacing: 1px;}


/* --------------------------------------------------------------------------
3. Link
------------------------------------------------------------------------- */

.link-color,a { text-decoration: none;}
.link-color {color: #ffffff !important;}
a:hover,a:focus { text-decoration: none; color: #ed1b24 !important;}

/* --------------------------------------------------------------------------
4. List
------------------------------------------------------------------------- */

.reset-list-style, .reset-list-style ul{ list-style: none;}
.overflowX-hidden{overflow-x: hidden;}

/* --------------------------------------------------------------------------
5. Image
------------------------------------------------------------------------- */

.bg-image { background-position: center; background-repeat: no-repeat; background-size: cover;}
/* --------------------------------------------------------------------------
6. Section
------------------------------------------------------------------------- */

.section-padding { padding: 50px 0; }
.section-top-padding { padding-top: 50px; }
.section-bottom-padding { padding-bottom: 50px; }


/* --------------------------------------------------------------------------
    7. Banner
    ------------------------------------------------------------------------- */
.banner-section{padding: 100px 0;}



/* --------------------------------Media Query-------------------------------- */


@media only screen and (max-width: 768px){	

    /* --------------------------------------------------------------------------
    1. Element Base
    ------------------------------------------------------------------------- */
    body { font-size: 17px;}
    h1, h2, h3, h4, h5, h6 {line-height: 1.25;}
    h1, .heading-size-1 { font-size: 30px; line-height: 40px !important;}
    h2, .heading-size-2 { font-size: 25px; line-height: 35px !important;}
    h3, .heading-size-3 { font-size: 23px; line-height: 33px !important;}
    h4, .heading-size-4 { font-size: 21px; line-height: 31px !important;}
    h5, .heading-size-5 { font-size: 20px; line-height: 30px !important;}
    h6, .heading-size-6 { font-size: 18px; line-height: 28px !important;}


    /* --------------------------------------------------------------------------
    2. Section
    ------------------------------------------------------------------------- */

   .section-padding { padding: 40px 0; }
   .section-top-padding { padding-top: 40px; }
   .section-bottom-padding { padding-bottom: 40px; }

   /* --------------------------------------------------------------------------
    3. Banner
    ------------------------------------------------------------------------- */
    .banner-section {padding: 80px 0;}
}



@media only screen and (max-width:575px){


    /* --------------------------------------------------------------------------
    1. Section
    ------------------------------------------------------------------------- */
    
    .section-padding { padding: 30px 0; }
    .section-top-padding { padding-top: 30px; }
    .section-bottom-padding { padding-bottom: 30px; }

    /* --------------------------------------------------------------------------
    2. Banner
    ------------------------------------------------------------------------- */
    .banner-section {padding: 70px 0;}
}





