/* home section  */

.home-section{
    background-image: url('./images/aboutus-banner.jpg'); 
}

/* carousel section  */

.citem-parent img{width:100px;height: 100px;background: rgb(34, 36, 36);z-index: 1;border-radius: 50%;
}
.citem-parent::before{content: "";width: 110px;height:110px;border-bottom-right-radius: 100%;background-color: #f44336;z-index: -1;position: absolute;top:0;left:0;
}
.citem-parent{height: 300px;box-shadow: -10px 10px 40px rgba(0,0,0,.25);position: relative;
}
.citem-parent-text1,.citem-parent-text2,.citem-parent-text3{z-index: -1;margin: 2rem;
}

/* ---bootstrap arrow---- */

.carousel-control-prev-icon,
.carousel-control-next-icon , .carousel-indicators {
    filter: invert(100%);
}
.carousel-control-prev{left:-20px;}
.carousel-control-next{right:-20px;}
.carousel-indicators [data-bs-target]{width: 12px!important;height: 12px!important;margin:0 15px!important;border-radius: 50%;
}
.carousel-indicators{bottom: -15px;}

